<template>
  <div class="v-cluster">
    <div class="crumbs">
      <span class="crumbs-item"></span>
      <span class="crumbs-item qingse-text"></span>
    </div>
    <div class="v-content">
      <div class="btns-group">
        <span class="comb-btn waves-effect lvse" @click="addNewCluster"></span>
        <router-link class="comb-btn waves-effect qingse" :to="{ name: 'AddHost'}"></router-link>
      </div>
      <div class="row _list-group">
        <div class="col m12 l6 xl4">
          <div class="_list-con">
            <div class="_list-con_title center-align qingse-text"></div>
            <div class="_list-con_desc center-align grey-text lighten-5"></div>
            <div class="_list-con_quan center-align">
              <percentage :used="2" :free="10" :lineWidth="20" :width="190" :height="190"></percentage>
            </div>
            <ul class="_list-con_infos">
              <li><i class="color-block qingse"></i> 2GB</li>
              <li><i class="color-block zise"></i> 10GB</li>
              <li>20170105</li>
            </ul>
            <div class="_list-con_btns">
              <router-link :to="{ name: 'Cluster-Details', params:{id:1} }" class="comb-btn waves-effect lvse "></router-link>
              <span class="comb-btn waves-effect qingse right" @click="delCluster(1)"></span>
            </div>
          </div>
        </div>
        <div class="col m12 l6 xl4">
          <div class="_list-con">
            <div class="_list-con_title center-align qingse-text"></div>
            <div class="_list-con_desc center-align grey-text lighten-5"></div>
            <div class="_list-con_quan center-align">
              <percentage :used="10" :free="60" :lineWidth="20" :width="190" :height="190"></percentage>
            </div>
            <ul class="_list-con_infos">
              <li><i class="color-block qingse"></i> 10GB</li>
              <li><i class="color-block zise"></i> 60GB</li>
              <li>20170105</li>
            </ul>
            <div class="_list-con_btns">
              <span class="comb-btn waves-effect lvse "></span>
              <span class="comb-btn waves-effect qingse right" @click="delCluster(2)"></span>
            </div>
          </div>
        </div>
      </div>
    </div>
    <!--  -->
    <modal title="" buttons="," buttonsClass="comb-btn lvse,comb-btn qingse" ref="addcluster" class="comb-dialog" @callback="addSure">
      <div class="_new-cluster_con">
        <div class="form-gird">
          <label class="label-control s12 m1 center-align"></label>
          <div class="col s12 m11">
            <input type="text" class="form-control" v-model="adddata.name">
          </div>
        </div>
        <div class="form-gird">
          <label class="label-control s12 m1 center-align"></label>
          <div class="col s12 m11">
            <textarea name="" class="form-control" v-model="adddata.desc"></textarea>
          </div>
        </div>
      </div>
    </modal>
    <!--  end -->
    <!--  -->
    <modal buttons="," buttonsClass="comb-btn lvse,comb-btn qingse" ref="delcluster" class="comb-dialog mini">
      <div class="comb-dialog_info center-align red-text large"><i class="ten-icon">&#xe691;</i> <span v-html="delbody"></span></div>
    </modal>
    <!--  end -->
  </div>
</template>

<script>
  export default {
    name: 'Cluster',
    data () {
      return {
        adddata: {
          name: '',
          desc: ''
        },
        delbody: ''
      }
    },
    methods: {
      addNewCluster () {
        this.$refs.addcluster.show()
      },
      delCluster (id) {
        this.delbody = '' + id + ''
        this.$refs.delcluster.show()
      },
      addSure () {
//        console.log(this.adddata.name)
      }
    }
  }
</script>

<style scoped lang="scss">
  @import "../scss/cluster";
</style>



